export function getUsers() {
  return users;
}

export function getUser(id) {
  return users.find((u) => u._id === id);
}

export function saveUser(user) {
  //edit
  let userInDb = users.find((m) => m._id === user._id) || {};
  userInDb.name = user.name;
  userInDb.genre = kelasAPI.kelas.find((g) => g._id === user.genreId);
  userInDb.numberInStock = user.numberInStock;
  userInDb.dailyRentalRate = user.dailyRentalRate;

  if (!userInDb._id) {
    userInDb._id = Date.now();
    users.push(userInDb);
  }

  return userInDb;
}

export function deleteUser(id) {
  let userInDb = users.find((u) => u._id === id);
  users.splice(users.indexOf(userInDb), 1);
  return userInDb;
}
